@charset "utf-8";

/*----------------------------------------------------

	font-familyの一覧ファイルです。
	基本変更することはありません。

----------------------------------------------------*/

/*----------------------------------------------------
  Noto Sans
*/
@font-face {
    font-family: 'Noto Sans Japanese';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/noto_sans/NotoSansCJKjp-Regular.woff') format('woff'),
    url('../fonts/noto_sans/NotoSansCJKjp-Regular.ttf')  format('truetype');
}
@font-face {
    font-family: 'Noto Sans Japanese';
    font-style: normal;
    font-weight: 700;
    src: url('../fonts/noto_sans/NotoSansCJKjp-Bold.woff') format('woff'),
    url('../fonts/noto_sans/NotoSansCJKjp-Bold.ttf')  format('truetype');
}

/*----------------------------------------------------
  Russo One
*/
@font-face {
  font-family: 'Russo One';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/Russo_One/RussoOne-Regular.ttf')  format('truetype');
}


// ゴシック(ベーシック)
$gothic-A: Arial, "Helvetica Neue",'Hiragino Kaku Gothic ProN',Osaka,メイリオ,Meiryo,"ＭＳ Ｐゴシック",sans-serif;

// ゴシック(今風)
$gothic-B: "Noto Sans Japanese", "ヒラギノ角ゴ ProN", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;

// 明朝
$mincho-A: "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", serif;