@charset "utf-8";

.formList {
  li {
    margin-bottom: 30px;
  }
  @include max-screen($bp-sp) {
    li {
      margin-bottom: 15px;
    }
  }
}

input, textarea {
  &.InputA {
    box-sizing: border-box;
    font-size: 18px;
    padding: 10px 20px;
    width: 100%;
  
    //リセット
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
    background-image: none;
    border: 1px solid $base-color;
    border-radius: 0;
    color: inherit;
    font-family: inherit;
    &::placeholder {
      color: $base-color;
    } 
    &:focus {
      border: 1px solid rgba(0, 0, 0, 0.32);
      box-shadow: none;
      outline: none;
    }
    &.InputA-ta {
      height: 235px;
    } 
    @include max-screen($bp-sp) {
      font-size: 16px;
      padding: 5px 10px;
      &.InputA-ta {
        height: 150px;
      } 
    }
  }
}

.submitBtn {
  font-size: 18px;
  padding: 15px 60px;

  //リセット
  -webkit-appearance: none;
  background-color: $base-color;
  background-image: none;
  border: none;
  border-radius: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  margin: 0;
  text-decoration: none;

  transition: .35s;
  &:hover, &:focus {
    outline: none;
  }
  &::-moz-foucus-inner {
    border: none;
    padding: 0;
  }

  &:hover {
    background-color: $main-color;
    transition: .15s;
  }
}